:root {
    --main-color: royalblue;
    --secondary-color: gray;
}

.SearchResultStyle h1 {
    color: var(--main-color);
    font-size: 2em;
    margin-top: 5vh;
}

.SearchResultStyle p {
    color: royalblue;
}

.LiStyle {
    list-style: none;
    padding-left: 0rem;
}


